.section {
  &__content {
    @include page-grid;
  }

  &__top {
    grid-column: 1 / -1;
    @include page-grid;
    margin-bottom: 60px;

    @include on-tablet {
      margin-bottom: 75px;
    }

    @include on-desktop {
      margin-bottom: 70px;
    }
  }

  &__title {
    margin: 0;
    font-size: 28px;
    font-weight: 700;
    line-height: 35px;
    grid-column: 1 / -1;

    @include on-tablet {
      font-size: 40px;
      line-height: 50px;
      grid-column: span 4;
    }

    @include on-desktop {
      font-size: 48px;
      line-height: 57.6px;
      grid-column: span 6;
    }
  }

  &__button {
    display: flex;
    border: 1px solid $main-color;
    height: 60px;
    width: 100%;
    color: $main-color;
    text-decoration: none;
    justify-content: center;
    align-items: center;

    @include hover (transform, scale(1.2));

    &--mobile {
      grid-column: 1 / -1;

      @include on-tablet {
        display: none;
      }
    }

    &--tablet-pc {
      display: none;

      @include on-tablet {
        display: flex;
        grid-column: -3 / -1;
      }

      @include on-desktop {
        grid-column: -4 / -1;
      }
    }
  }

  &__arrow {
    margin-left: 8px;
  }

  &__dates {
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    color: $main-color;
    margin: 0;
  }

  &__subtitle {
    margin: 0;
    font-size: 20px;
    font-weight: 700;
    line-height: 25px;
    margin-top: 10px;

    @include on-tablet {
      font-size: 24px;
      line-height: 30px;
    }
  }

  &__desc {
    margin: 0;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
  }
}
