.footer {
  background-color: $header-menu-bg-color;
  padding-block: 34px 30px;

  @include on-tablet {
    padding-top: 50px;
  }

  &__content {
    @include page-grid;
  }

  &__contacts {
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: span 2;
    }

    @include on-desktop {
      grid-column: span 3;
    }
  }

  &__title {
    margin: 0;
    color: $main-bg-color;
    font-size: 18px;
    font-weight: 700;
    line-height: 27px;
    margin-bottom: 10px;

    @include on-tablet {
      font-size: 20px;
      line-height: 30px;
      margin-bottom: 30px;
    }
  }

  &__contact {
    text-decoration: none;
    color: $main-bg-color;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    display: inline-block;
    @include hover (transform, scale(1.2));
  }

  &__socials {
    margin-top: 30px;
    display: flex;
    gap: 20px;

    @include on-tablet {
      margin-top: 50px;
    }
  }

  &__social {
    &-img {
      height: 20px;
      width: 20px;

      @include hover (transform, scale(1.2));
    }
  }

  &__schedule {
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: 3 / 5;
    }

    @include on-desktop {
      grid-column: 5 / 9;
    }
  }

  &__menu {
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: 5 / -1;
    }

    @include on-desktop {
      grid-column: 9 / -1;
    }
  }

  &__desc {
    color: $main-bg-color;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
  }

  &__list {
    gap: 5px;

    @include on-tablet {
      gap: 10px;
    }
  }

  &__link {
    display: inline-block;
    @include hover (transform, scale(1.2));
  }

  &__bottom {
    margin-top: 50px;
    grid-column: 1 / -1;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include on-desktop {
      margin-top: 60px;
    }
  }

  &__text {
    display: flex;
    align-items: center;
    gap: 22px;

    @include on-desktop {
      gap: 102px;
    }
  }

  &__copyright {
    color: $main-bg-color;
    font-size: 14px;
    font-weight: 400;
    line-height: 19.6px;
  }

  &__privacy {
    text-decoration: none;
    color: $main-bg-color;
    font-family: Raleway;
    font-size: 14px;
    font-weight: 400;
    line-height: 19.6px;

    @include hover (transform, scale(1.2));
  }

  &__arrow {
    &-img {
      height: 30px;
      width: 30px;
      @include hover (transform, scale(1.2));
    }
  }
}
