.begin {
  background-color: $header-menu-bg-color;
  position: relative;
  margin-bottom: 90px;

  @include on-tablet {
    margin-bottom: 120px;
  }

  @include on-desktop {
    margin-bottom: 110px;
  }
  
  &__content {
    @include page-grid;
    height: 416px;
    align-content: center;

    @include on-tablet {
      height: 515px;
    }

    @include on-desktop {
      height: 800px;
    }
  }

  &__title {
    color: $main-bg-color;
    margin: 0;
    grid-column: 1 / -1;
    font-size: 38px;
    font-weight: 700;
    line-height: 38px;
    margin-bottom: 20px;

    @include on-tablet {
      grid-column: 1 / 5;
      font-size: 62px;
      line-height: 62px;
    }

    @include on-desktop {
      grid-column: 6 / -1;
      font-size: 80px;
      line-height: 80px;
      margin-bottom: 10px;
    }
  }

  &__desc {
    color: $main-bg-color;
    margin: 0;
    grid-column: 1 / -1;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    margin-bottom: 40px;

    @include on-tablet {
      grid-column: 1 / 5;
      margin-bottom: 60px;
    }

    @include on-desktop {
      grid-column: 6 / -3;
      margin-bottom: 50px;
    }
  }

  &__button {
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: span 3;
    }

    @include on-desktop {
      grid-column: 6 / 10;
    }
  }

  &__img {
    display: none;

    @include on-desktop {
      position: absolute;
      display: inline-block;
      top: 0;
      left: 0;
      height: 800px;
      width: 578px;
    }
  }
}
