.menu {
  &__main {
    @include page-grid;
    color: $main-bg-color;

    @include on-desktop {
      height: calc(100vh - 80px);
    }
  }

  &__event {
    &-time {
      grid-column: 1 / -1;
      margin-bottom: 20px;

      @include on-tablet {
        grid-column: span 2;
        margin-bottom: 50px;
      }

      @include on-desktop {
        margin-bottom: 70px;
      }
    }

    &-address {
      grid-column: 1 / -1;
      margin-bottom: 40px;

      @include on-tablet {
        grid-column: 3 / 6;
      }
    }
  }

  &__title {
    font-size: 14px;
    font-weight: 400;
    line-height: 19.6px;
    margin: 0;
    color: $main-bg-color;
  }

  &__info {
    font-size: 16px;
    font-weight: 600;
    line-height: 24px;
    margin: 0;
    color: $main-bg-color;
  }

  &__button {
    grid-column: 1 / -1;

    @include on-tablet {
      margin-bottom: 2px;
      grid-column: 1 / 4;
    }

    @include on-desktop {
      margin: 0;
    }
  }

  &__img {
    display: none;

    @include on-tablet {
      display: inline-block;
    }
  }

  &__nav {
    grid-column: 1 / -1;

    @include on-desktop {
      grid-column: 1 / 7;
    }

    &::after {
      content: '';
      display: block;
      width: 100%;
      height: 1px;
      border-bottom: 1px solid $main-bg-color;
      margin-block: 40px;

      @include on-tablet {
        margin-block: 50px;
      }

      @include on-desktop {
        margin-block: 60px;
      }
    }
  }

  &__list {
    gap: 20px;

    @include on-tablet {
      gap: 30px;
    }

    @include on-desktop {
      gap: 60px;
    }
  }

  &__link {
    font-family: 'Playfair Display', serif;
    font-size: 20px;
    font-weight: 700;
    line-height: 30px;

    @include on-tablet {
      font-size: 48px;
      line-height: 57.6px;
    }

    @include hover(text-decoration, underline);
  }
}
