.exhibitions {
  margin-bottom: 120px;

  @include on-tablet {
    margin-bottom: 160px;
  }

  @include on-desktop {
    margin-bottom: 200px;
  }

  &__top {
    margin-block: 90px 60px;

    @include on-tablet {
      margin-block: 105px 75px;
    }

    @include on-desktop {
      margin-block: 110px 70px;
    }
  }

  &__exhibition {
    @include page-grid;
    grid-column: 1 / -1;

    @include on-tablet {
      --columns: 3;
    }

    @include on-desktop {
      --columns: 6;
    }

    &--1 {
      margin-bottom: 60px;

      @include on-tablet {
        margin-bottom: 0;
        grid-column: span 3;
      }

      @include on-desktop {
        grid-column: span 6;
      }
    }

    &--2 {
      margin-bottom: 30px;
      @include on-tablet {
        margin-bottom: 0;
        grid-column: -4 / -1;
      }

      @include on-desktop {
        grid-column: -7 / -1;
      }
    }
  }
}
