.page {
  scroll-behavior: smooth;
  background: #f1f5f4;
  margin: 0;
}

.page__body {
  margin: 0;
}

.page__menu {
  box-sizing: border-box;
  opacity: 0;
  pointer-events: none;
  background-color: #3f5252;
  height: 100vh;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(100%);
}

.page__menu:target {
  z-index: 2;
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

h1, h2, h3, h4, h5, h6 {
  color: #1c1b29;
  font-family: Playfair Display, serif;
}

p {
  color: #504f5e;
  font-family: Raleway, sans-serif;
}

.container {
  margin: 0 auto;
  padding-inline: 20px;
}

@media (width >= 768px) {
  .container {
    padding-inline: 39px;
  }
}

@media (width >= 1280px) {
  .container {
    max-width: 1280px;
    padding-inline: 55px;
  }
}

.top-bar {
  justify-content: space-between;
  align-items: center;
  height: 80px;
  display: flex;
}

.top-bar__logo {
  background-image: url("logo.b6e7e122.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 123px;
  height: 40px;
  display: inline-block;
}

.top-bar__menus {
  align-items: center;
  gap: 40px;
  display: flex;
}

.top-bar__menu {
  display: inline-block;
}

@media (width >= 1280px) {
  .top-bar__menu {
    width: 30px;
    height: 30px;
  }
}

.top-bar__menu--burger {
  background-image: url("burger-menu.fbe79bf9.svg");
  width: 25px;
  height: 20px;
}

.top-bar__menu--cross {
  background-image: url("menu-cross.39bf405a.svg");
  width: 22px;
  height: 22px;
}

.top-bar__language {
  color: #f1f5f4;
  align-items: center;
  font-family: Raleway, sans-serif;
  font-size: 16px;
  line-height: 24px;
  text-decoration: none;
}

.top-bar__language--mobile {
  display: flex;
}

@media (width >= 768px) {
