.exhibition {
  &__img {
    height: 356;
    width: 100%;
    grid-column: 1 / -1;
    object-fit: cover;
    object-position: center;
    @include hover (transform, scale(1.2));

    @include on-tablet {
      height: 420px;
    }

    @include on-desktop {
      height: 484px;
    }
  }

  &__dates {
    margin: 0;
    grid-column: 1 / -1;
    margin-top: 20px;

    @include on-tablet {
      margin-top: 30px;
    }

    @include on-desktop {
      margin-top: 20px;
    }
  }

  &__title {
    grid-column: 1 / -1;
  }

  &__desc {
    grid-column: 1 / -1;
    margin-top: 20px;
  }

  &__button {
    margin-top: 30px;
    grid-column: 1 / -1;

    @include on-tablet {
      margin-top: 40px;
    }

    @include on-desktop {
      margin-top: 50px;
      grid-column: 1 / 4;
    }
  }
}
