.one-news {
  grid-column: 1 / -1;
  display: flex;
  flex-direction: column;

  &--1 {
    margin-bottom: 50px;

    @include on-tablet {
      grid-column: span 3;
      margin: 0;
    }

    @include on-desktop {
      grid-column: span 6;
    }
  }

  &--2 {
    @include on-tablet {
      grid-column: 4 / -1;
    }

    @include on-desktop {
      grid-column: 7 / -1;
    }
  }

  &__img {
    width: 100%;
    height: 281px;
    object-fit: cover;
    object-position: center;
    margin-bottom: 20px;

    @include hover (transform, scale(1.2));

    @include on-tablet {
      height: 330px;
      margin-bottom: 30px;
    }

    @include on-desktop {
      height: 370px;
      margin-bottom: 20px;
    }
  }

  &__date {
    font-size: 14px;
    font-weight: 400;
    line-height: 19.6px;
    color: $black;
    margin: 0;
  }

  &__title {
    font-size: 20px;
    font-weight: 700;
    line-height: 25px;
    margin: 0;
    margin-bottom: 20px;

    @include on-tablet {
      line-height: 30px;
      margin-bottom: 18px;
    }
  }
}
